.footer {
    text-align: center;
    background-color: #f7f7f7;
    width: 100%;
    padding: 30px;
    color: #545454;
    font-size: 16px;
    line-height: 1.19;
}
.footer__icon_logo {
    animation: heartbeat 1250ms cubic-bezier(.25, .1, .25, 1) infinite;
  }
  .animate{
    animation: beatheart 1250ms cubic-bezier(.25, .1, .25, 1) infinite;
  }

.footer a {
    text-decoration: underline;
    color: #545454;
}

@keyframes heartbeat {
  0% {
          transform: scale(1);
      }
  
      10% {
          transform: scale(1.4);
      }
  
      50% {
          transform: scale(1);
      }
  
      100% {
          transform: scale(1);
      }
}

@keyframes beatheart {
  0% {
          transform: scale(1);
      }
  
      10% {
          transform: scale(1);
      }
  
      50% {
          transform: scale(1.4);
      }
  
      100% {
          transform: scale(1);
      }
}